html {
	font-size: 62.5%;
	font-family: 'Source Sans', sans-serif;
}

a {
	color: indigo;
	text-decoration: none;
	&:hover {
		color: $silver-blue;
	}
}

/* Titles */
h1,
h2,
h3,
h4,
h5 {
	font-family: 'Lato', sans-serif;
	color: $dark-accent;
	margin: 0;
	padding: 0;
}


h1 {
	font-size: 12rem;
	color: #fff;
}

h2 {
	font-size: 5rem;
	// color: #001832;
}

h3 {
	padding-bottom: 1.5%;
	font-size: 6rem;
}

h4 {
	font-size: 3.25rem;
}

h5 {
	font-size: 3rem;
}

/* paragraphs */

p {
	color: #333;
	font-family: 'Source Sans', sans-serif;
	line-height: 1.5;
	font-size: 1.75rem;
}

/*blockquotes*/

blockquote {
	background: #F7F7F7;
	margin: 0;
	padding: 20px;
	p {
		margin-top: 0;
	}
}

/*tables*/

table {
	td {
		padding: 10px;
	}
	tr td:first {
		padding-right: 0;
	}
}

/*def lists*/

dt {
	font-weight: 600;
	padding-bottom: 5px;
}

dd {
	padding: 10px;
	margin-left: 10px;
}

/*lists*/

ul li, ol li {
	padding: 3px;
}

/*PRE*/

pre {
	background: #F7F7F7;
	padding: 10px;
	border-radius: 4px;
	border: 1px solid #ccc;
}


/*
====================================
Global Classes / Wordpress Specific
====================================
*/

.aligncenter {
	text-align: center;
	margin: 0 auto;
}

.alignleft {
	float: left;
}

.alignright {
	float: right;
}

img {
	&.alignleft {
		padding-right: 5px;
	}
	&.alignright {
		padding-left: 5px;
	}
}

.clear {
	width: 100%;
	float: left;
	clear: both;
}